import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';

// Simple page components
const Header = () => (
  <header className="header">
    <h1> Cannabis Delivery Platform</h1>
  </header>
);

const Footer = () => (
  <footer className="footer">
    <p>&copy; 2025 Cannabis Delivery Platform. All rights reserved.</p>
  </footer>
);

const Home = () => (
  <div className="page">
    <h2>Welcome to Cannabis Delivery</h2>
    <p>Your trusted online cannabis delivery service.</p>
    <p>Browse our selection of products, add items to your cart, and get them delivered to your door.</p>
  </div>
);

const Products = () => (
  <div className="page">
    <h2>Our Products</h2>
    <p>Browse our wide selection of cannabis products.</p>
  </div>
);

const ProductDetail = () => (
  <div className="page">
    <h2>Product Details</h2>
    <p>Detailed information about the selected product.</p>
  </div>
);

const Cart = ({ cart }) => (
  <div className="page">
    <h2>Shopping Cart</h2>
    <p>Items in cart: {cart.length}</p>
  </div>
);

const Checkout = () => (
  <div className="page">
    <h2>Checkout</h2>
    <p>Complete your order here.</p>
  </div>
);

const Orders = () => (
  <div className="page">
    <h2>My Orders</h2>
    <p>View your order history.</p>
  </div>
);

const Login = () => (
  <div className="page">
    <h2>Login</h2>
    <p>Sign in to your account.</p>
  </div>
);

const Register = () => (
  <div className="page">
    <h2>Register</h2>
    <p>Create a new account.</p>
  </div>
);

function App() {
  const [cart, setCart] = useState([]);

  return (
    <Router>
      <div className="container">
        <Header />
        <main>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/products" element={<Products />} />
            <Route path="/products/:id" element={<ProductDetail />} />
            <Route path="/cart" element={<Cart cart={cart} />} />
            <Route path="/checkout" element={<Checkout />} />
            <Route path="/orders" element={<Orders />} />
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
          </Routes>
        </main>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
